"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Check, UserPlus, User } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { toast } from "sonner"
import { handleRpcError } from "@/lib/error-handler"

export default function RegisterTab({ campaignInfo, userIsRegistered, onRegister, campaignId }) {
  const { contract } = useContract()
  const [activeTab, setActiveTab] = useState("voter")
  const [voterPassKey, setVoterPassKey] = useState("")
  const [isSubmittingVoter, setIsSubmittingVoter] = useState(false)

  const [candidateForm, setCandidateForm] = useState({
    name: "",
    statement: "",
    imageHash: "",
    passKey: "",
  })
  const [isSubmittingCandidate, setIsSubmittingCandidate] = useState(false)

  const handleCandidateChange = (e) => {
    const { name, value } = e.target
    setCandidateForm((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleVoterRegister = async (e) => {
    e.preventDefault()
    if (!contract || isSubmittingVoter) return

    try {
      setIsSubmittingVoter(true)

      // Call the contract's registerToVote function
      const tx = await contract.registerToVote(campaignId, voterPassKey)

      toast.promise(tx.wait(), {
        loading: "Registering as voter...",
        success: "Successfully registered as a voter!",
        error: "Failed to register as a voter",
      })

      await tx.wait()

      // Clear form and refresh data
      setVoterPassKey("")
      onRegister()
    } catch (error) {
      console.error("Error registering as voter:", error)
      handleRpcError(error)
    } finally {
      setIsSubmittingVoter(false)
    }
  }

  const handleCandidateRegister = async (e) => {
    e.preventDefault()
    if (!contract || isSubmittingCandidate) return

    try {
      setIsSubmittingCandidate(true)

      // Call the contract's registerAsCandidate function
      const tx = await contract.registerAsCandidate(
        campaignId,
        candidateForm.name,
        candidateForm.statement,
        candidateForm.imageHash,
        candidateForm.passKey,
      )

      toast.promise(tx.wait(), {
        loading: "Registering as candidate...",
        success: "Successfully registered as a candidate!",
        error: "Failed to register as a candidate",
      })

      await tx.wait()

      // Clear form and refresh data
      setCandidateForm({
        name: "",
        statement: "",
        imageHash: "",
        passKey: "",
      })
      onRegister()
    } catch (error) {
      console.error("Error registering as candidate:", error)
      handleRpcError(error)
    } finally {
      setIsSubmittingCandidate(false)
    }
  }

  // If user is already registered
  if (userIsRegistered) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Registration</CardTitle>
          <CardDescription>You are already registered for this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="border-green-200 bg-green-50 dark:bg-green-950/20 dark:border-green-900">
            <Check className="h-4 w-4 text-green-600 dark:text-green-400" />
            <AlertDescription className="text-green-800 dark:text-green-300 ml-2">
              You have successfully registered for this campaign. You can participate when voting begins.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Register for Campaign</CardTitle>
        <CardDescription>Register to participate in this voting campaign</CardDescription>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="voter" value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="grid w-full grid-cols-2 mb-6">
            <TabsTrigger value="voter" className="flex items-center gap-2">
              <User className="h-4 w-4" />
              <span>Register as Voter</span>
            </TabsTrigger>
            {campaignInfo.votingType === 0 && (
              <TabsTrigger value="candidate" className="flex items-center gap-2">
                <UserPlus className="h-4 w-4" />
                <span>Register as Candidate</span>
              </TabsTrigger>
            )}
          </TabsList>

          <TabsContent value="voter">
            <form onSubmit={handleVoterRegister}>
              <div className="space-y-4">
                {campaignInfo.restriction === 2 && (
                  <div className="space-y-2">
                    <Label htmlFor="voterPassKey">Passkey</Label>
                    <Input
                      id="voterPassKey"
                      type="password"
                      placeholder="Enter the campaign passkey"
                      value={voterPassKey}
                      onChange={(e) => setVoterPassKey(e.target.value)}
                      required={campaignInfo.restriction === 2}
                    />
                  </div>
                )}

                <Button
                  type="submit"
                  className="w-full"
                  disabled={(campaignInfo.restriction === 2 && !voterPassKey) || isSubmittingVoter}
                >
                  {isSubmittingVoter ? "Registering..." : "Register as Voter"}
                </Button>
              </div>
            </form>
          </TabsContent>

          {campaignInfo.votingType === 0 && (
            <TabsContent value="candidate">
              <form onSubmit={handleCandidateRegister}>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="name">Name</Label>
                    <Input
                      id="name"
                      name="name"
                      placeholder="Your candidate name"
                      value={candidateForm.name}
                      onChange={handleCandidateChange}
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="statement">Statement</Label>
                    <Textarea
                      id="statement"
                      name="statement"
                      placeholder="Share your platform or statement"
                      value={candidateForm.statement}
                      onChange={handleCandidateChange}
                      required
                      rows={3}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="imageHash">Image URL (Optional)</Label>
                    <Input
                      id="imageHash"
                      name="imageHash"
                      placeholder="IPFS hash or URL to your profile image"
                      value={candidateForm.imageHash}
                      onChange={handleCandidateChange}
                    />
                  </div>

                  {campaignInfo.restriction === 2 && (
                    <div className="space-y-2">
                      <Label htmlFor="passKey">Passkey</Label>
                      <Input
                        id="passKey"
                        name="passKey"
                        type="password"
                        placeholder="Enter the campaign passkey"
                        value={candidateForm.passKey}
                        onChange={handleCandidateChange}
                        required={campaignInfo.restriction === 2}
                      />
                    </div>
                  )}

                  <Button
                    type="submit"
                    className="w-full"
                    disabled={
                      !candidateForm.name ||
                      !candidateForm.statement ||
                      (campaignInfo.restriction === 2 && !candidateForm.passKey) ||
                      isSubmittingCandidate
                    }
                  >
                    {isSubmittingCandidate ? "Registering..." : "Register as Candidate"}
                  </Button>
                </div>
              </form>
            </TabsContent>
          )}
        </Tabs>
      </CardContent>
    </Card>
  )
}

