"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, Check } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { shortenAddress } from "@/lib/utils"
import { toast } from "sonner"
import { handleRpcError } from "@/lib/error-handler"

export default function VoteTab({
  campaignInfo,
  proposals,
  candidates,
  isConnected,
  userIsRegistered,
  userHasVoted,
  needsRegistration,
  address,
  onVote,
  campaignId,
}) {
  const { contract } = useContract()
  const [selectedItem, setSelectedItem] = useState(null)
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleVote = async () => {
    if (!contract || selectedItem === null || isSubmitting) return

    try {
      setIsSubmitting(true)

      let tx
      if (campaignInfo.votingType === 1 /* ProposalBased */) {
        // Vote for proposal
        tx = await contract.voteForProposal(campaignId, selectedItem)
      } else {
        // Vote for candidate
        tx = await contract.voteForCandidate(campaignId, selectedItem)
      }

      toast.promise(tx.wait(), {
        loading: "Submitting your vote...",
        success: "Your vote has been recorded!",
        error: "Failed to submit vote",
      })

      await tx.wait()

      // Refresh data
      onVote()
    } catch (error) {
      console.error("Error voting:", error)
      handleRpcError(error)
    } finally {
      setIsSubmitting(false)
    }
  }

  // If user is not connected, show connect wallet message
  if (!isConnected) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Voting</CardTitle>
          <CardDescription>Connect your wallet to participate in this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
            <AlertCircle className="h-4 w-4 text-amber-600 dark:text-amber-400" />
            <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
              You need to connect your wallet to participate in this voting campaign.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  // If registration is required but user is not registered
  if (needsRegistration && !userIsRegistered) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Voting</CardTitle>
          <CardDescription>Registration required to vote</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
            <AlertCircle className="h-4 w-4 text-amber-600 dark:text-amber-400" />
            <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
              You need to be registered to participate in this voting campaign. Please go to the Register tab to
              register.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  // If user has already voted
  if (userHasVoted) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Voting</CardTitle>
          <CardDescription>You have already cast your vote</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="border-green-200 bg-green-50 dark:bg-green-950/20 dark:border-green-900">
            <Check className="h-4 w-4 text-green-600 dark:text-green-400" />
            <AlertDescription className="text-green-800 dark:text-green-300 ml-2">
              Your vote has been recorded. You can view the current results in the Results tab.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Cast Your Vote</CardTitle>
        <CardDescription>
          Select {campaignInfo.votingType === 0 ? "a candidate" : "a proposal"} to vote for
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {campaignInfo.votingType === 1 /* ProposalBased */
            ? // Proposal voting UI
              proposals.map((proposal) => (
                <div
                  key={proposal.id}
                  className={`p-4 rounded-lg border-2 cursor-pointer transition-colors ${
                    selectedItem === proposal.id
                      ? "border-primary bg-primary/5"
                      : "border-border hover:border-primary/30 hover:bg-muted/50"
                  }`}
                  onClick={() => setSelectedItem(proposal.id)}
                >
                  <div className="flex items-start justify-between">
                    <div className="space-y-1">
                      <h3 className="font-medium">Proposal {proposal.id + 1}</h3>
                      <p className="text-sm text-muted-foreground">{proposal.content}</p>
                    </div>
                    {selectedItem === proposal.id && <Badge className="ml-2">Selected</Badge>}
                  </div>
                </div>
              ))
            : // Candidate voting UI
              candidates.map((candidate) => (
                <div
                  key={candidate.id}
                  className={`p-4 rounded-lg border-2 cursor-pointer transition-colors ${
                    selectedItem === candidate.id
                      ? "border-primary bg-primary/5"
                      : "border-border hover:border-primary/30 hover:bg-muted/50"
                  }`}
                  onClick={() => setSelectedItem(candidate.id)}
                >
                  <div className="flex items-start justify-between">
                    <div className="space-y-1">
                      <h3 className="font-medium">{candidate.name}</h3>
                      <p className="text-xs text-muted-foreground">{shortenAddress(candidate.address)}</p>
                      <p className="text-sm text-muted-foreground">{candidate.statement}</p>
                    </div>
                    {selectedItem === candidate.id && <Badge className="ml-2">Selected</Badge>}
                  </div>
                </div>
              ))}
        </div>
      </CardContent>
      <CardFooter>
        <Button className="w-full" disabled={selectedItem === null || isSubmitting} onClick={handleVote}>
          {isSubmitting ? "Submitting..." : "Submit Vote"}
        </Button>
      </CardFooter>
    </Card>
  )
}

